var IRm$ = {};

IRm$.locale = [];

IRm$.r1 = function(rcsid, rvalue, dvalue) {
	if (!IRm$.currentLocale)
	{
		var i;
		
		IRm$.currentLocale = IRm$.locale[0];
		
		for (i=0; i < IRm$.locale.length; i++)
		{
			if (IRm$.locale[i].name == window.useLocale)
			{
				IRm$.currentLocale = IRm$.locale[i];
				break;
			}
		}
	}
	
	var r = rcsid; // ? (IRm$.currentLocale.data[rcsid] || '!RESOURCE_EMPTY!') : null;
	
	if (IRm$.currentLocale && IRm$.currentLocale.data[rcsid])
	{
		r = ig$.vmode == 1 ? rcsid + "*" : IRm$.currentLocale.data[rcsid];
	}
	else
	{
		r = (ig$.vmode == 1 ? "x " : "") + (dvalue ? dvalue : rcsid);
	}
	
	if (r && rvalue)
	{
		if (typeof(rvalue) == "string")
		{
			rvalue = [rvalue];
		}
		
		if (rvalue.length == 1)
		{
			r = r.replace('@', rvalue[0]);
		}
		else
		{
			for (i=0; i < rvalue.length; i++)
			{
				r = r.replace("@" + (i+1), rvalue[i]);
			}
		}
	}
	return r;
}

IRm$.r2_d/*loadResources*/ = function(bproc) {
	if (bproc && bproc.func)
	{
		bproc.func.call(bproc.scope);
	}
}

IRm$.r2/*loadResources*/ = function(bproc, b_sysconf) {
	var me = null,
		req = new IG$._rpc$(),
		lc = window.useLocale;
	
	IRm$.r3/*checkCuddler*/();
		
	req.init(me, 
		{
			ack: "content",
			payload: {uid: "/SYS_Config/locale/" + lc},
			mbody: {},
			mts: "ROOT"
		}, me, function(jdoc) {
			var t = jdoc,
				// tnodes,
				i, //j, snodes,
				// loc, sname, svalue,
				// sloc, 
				tloc;
				
			if (t && t.data)
			{
				tloc = {
					name: t.name,
					data: {}
				};
				
				for (i=0; i < t.data.length; i++)
				{
					tloc.data[t.data[i].name] = t.data[i].value;
				}
				
				IRm$.locale.push(tloc);
			}
			
			if (b_sysconf === false)
			{
				if (bproc && bproc.func)
				{
					bproc.func.call(bproc.scope);
				}
			}
			else
			{
				// getting ui configuration from system
				var req = new IG$._rpc$();
				req.init(me, 
					{
						ack: "content",
						payload: {uid: "/SYS_Config/ui_config"},
						mbody: {},
						mts: window.m$mts || "ROOT"
					}, me, function(jdoc) {
						if (jdoc.config)
						{
							$.each(jdoc.config, function(k, v) {
								ig$[k] = v;
							});
						}
						
						if (bproc && bproc.func)
						{
							bproc.func.call(bproc.scope);
						}
					}, function() {
						// if failed load default values
						if (bproc && bproc.func)
						{
							bproc.func.call(bproc.scope);
						}
					}
				);
				
				req.send();
			}
		}, function() {
			if (bproc && bproc.func)
			{
				bproc.func.call(bproc.scope);
			}
		});
	req.send();
}

IRm$.r3/*checkCuddler*/ = function() {
	var me = null,
		req = new IG$._rpc$();
	req.init(me, 
		{
			ack: "collaborate",
			payload: {cmd: "request_channel", istest: "T"},
			mbody: {},
			mts: "ROOT"
		}, me, function(jdoc) {
			$("#robo_wrap").show();
		}, function() {
			$("#robo_wrap").hide();
		});
	req.showerror = false;
	req.send();
}
