/**
 * @module common/mainapp_webix
 * @desc common module for main application with webix framework 
 */
IG$._berr = 0;

/**
 * show error message
 * @memberof module:common/mainapp_webix
 * @param jdoc {object} error messages from server
 * @param parent {object} framework element for modal
 * @param params {object} options
 */
IG$.ShowErrorMessage = function(jdoc, parent, params) {
	var errcode = jdoc.errorcode,
		errdesc = jdoc.errormsg,
		errmsg = jdoc.detail || null,
		errstack = jdoc.stacktrace || null,
		pop,
		merror = false;
		
	switch (errcode)
	{
	case "0x7500":
	case "0x6d00":
		merror = true;
		break;
	case "0x8f00":
		// password error
		if (errmsg)
		{
			var em = errmsg.split("|"),
				emm = ["<br/><p>"];
			
			$.each(em, function(i, t) {
				var st;
				
				if (!t)
					return;
					
				if (t.indexOf("L_PWD_F_SPEC_CHAR_") > -1)
				{
					st = t.substring("L_PWD_F_SPEC_CHAR_".length);
					t = "L_PWD_F_SPEC_CHAR";
				}
				else if (t.indexOf("L_PWD_F_LEN_") > -1)
				{
					st = t.substring("L_PWD_F_LEN_".length);
					t = "L_PWD_F_LEN";
				}
				emm.push(IRm$.r1(t, st) + "<br/>");
			});
			
			emm.push("</p>");
			errmsg = emm.join("");
		}
		break;
	case "0x8d00":
		// too many password error
		break;
	}
	
	if (merror == false && (errmsg || errstack || params))
	{
//		pop = new IG$.ErrorDialog({
//			messagecontent: {
//				errdesc: errdesc,
//				errmsg: errmsg,
//				errstack: errstack,
//				params: params
//			}
//		});
//		pop.show();

		IG$.alertmsg(ig$.appname, errdesc + "<br/>\n\n" + (errmsg || ""), null, parent, 1, "error", null, {
			errdesc: errdesc,
			errmsg: errmsg,
			errstack: errstack,
			params: params
		}, null, 1);
	}
	else
	{
		IG$.alertmsg(ig$.appname, errdesc + "\n\n" + (errmsg || ""), null, parent, 1, "error", null, null, null, 1);
	}
}

/**
 * internal grid filtering
 * @memberof module:common/mainapp_webix
 * @param panel {object} parent panel object
 * @param gridname {string} grid name to filter
 * @param dindex {integer} integer value for filter column
 * @param fcomp {object} filtering textfield element
 */
IG$._dofilter = function(panel, gridname, dindex, fcomp) {
	var grd = panel.down("[name=" + gridname + "]") || panel,
		_filter,
		i;
	
	if (!fcomp)
	{
		fcomp = grd.down("[name=_filter]"); 
	}
	_filter = fcomp ? fcomp.getValue() : null;
	
	if (!_filter)
	{
		if (fcomp)
		{
			fcomp.markInvalid("Required!");
		}
		// for (i=0; i < dindex.length; i++)
		// {
			grd.store.removeFilter({
				dataIndex: dindex, // [i],
				operator: "like"
			});
		// }
	}
	else
	{
		if (fcomp)
		{
			fcomp.clearInvalid();
		}
		// for (i=0; i < dindex.length; i++)
		// {
			grd.store.addFilter({
				dataIndex: dindex,
				operator: "like",
				value: _filter
			});
		// }
	}
}

