/**
 * show dialog main function : master management for popup dialogs
 * popup dialog are maintained in IG$._popupDlgs
 * @memberof module:common/mainapp_webix
 * @param panel {object} parent panel object
 * @param dlg {object} webix dialog object
 */
IG$.showDlg = function(panel, dlg) {
	IG$._popupDlgs = IG$._popupDlgs || [];
	IG$._popupDlgs.push(dlg);
	
	dlg.show();
}

/**
 * popup dialog with login check
 * @memberof module:common/mainapp_webix
 * @param panel {object} parent panel object
 * @param dlg {object} webix dialog object
 * @param func {function} callback function
 * @param option {object} options
 */
IG$.checkLogin = function(panel, dlg, func, option) {
	var req = new IG$._rpc$();
	
	if (panel && panel.setLoading)
	{
		panel.setLoading(true);
	}
	
	req.checklogin = true;
	IG$._popupDlgs = IG$._popupDlgs || [];
	
	req.init(panel, 
		{
			ack: "ping",
			payload: {},
			mbody: {}
		}, panel, function(jdoc) {
			if (panel && panel.setLoading)
			{
				panel.setLoading(false);
			}
			
			if (dlg)
			{
				IG$._popupDlgs.push(dlg);
				dlg.show(null, option);
			}
			
			if (func)
			{
				func.call(panel);
			}
		});
	req.send();
};

/**
 * main popup alert message handler
 * @memberof module:common/mainapp_webix
 */
IG$.alertmsg = function(title, msg, fn, parent, ismodal, mtype, stack, messagecontent, btn, is_error) {
//	if (ismodal == 2)
//	{
//		var msgbox = new Ext.window.MessageBox(),
//			config = {
//			title : title || ig$.appname,
//			msg : msg,
//			buttons: msgbox.OK,
//			fn: fn || null,
//			scope : null,
//			minWidth: msgbox.minWidth,
//			modal: true,
//			animateTarget: (parent && parent.el) ? parent.el.dom : (parent && parent.body) ? parent.body.dom : null
//		};
//		
//		msgbox.show(config);
//		
//		return;
//	}

	if (ismodal == 2)
	{
		btn = 1;
	}
	
	if (is_error && IG$._berr)
	{
		// append errors
		IG$._berr.err.push({
			mtype: mtype,
			stack: stack,
			parent: parent,
			messagecontent: messagecontent
		});
		
		$(".cnt", IG$._berr.h).text(IG$._berr.err.length).show();
		return;
	}
	
	var body = $("body"),
		msgbox = $("<div class='igc-alert-cnt'>" + (ismodal ? "<div class='igc-alert-bg'></div>" : "") + "<div class='igc-alert-body'></div></div>").appendTo(body),
		mbody = $(".igc-alert-body", msgbox),
		bbox,
		bclose, bdetail,
		h = IRm$.r1("M_UNKNOWN"),
		pd = "", k,
		msgpop,
		binfo = "", bk,
		browser = window.bowser;
		
	if (is_error)
		IG$._berr = {
			h: msgbox,
			err: [
				{
					mtype: mtype,
					stack: stack,
					parent: parent,
					messagecontent: messagecontent
				}
			]
		};
		
	mtype = mtype || "warning";
	
	switch (mtype)
	{
	case "error":
		h = IRm$.r1("M_ERROR");
		break;
	case "warning":
		h = IRm$.r1("M_WARNING");
		break;
	case "info":
		h = IRm$.r1("M_INFO");
		break;
	case "success":
		h = IRm$.r1("M_SUCCESS");
		break;
	}
	
	if (messagecontent)
	{
		if (messagecontent.params)
		{
			for (k in messagecontent.params)
			{
				if (pd)
				{
					pd += "\n";
				}
				
				pd += k + ": " + messagecontent.params[k];
			}
			
			pd += "\n\n";
			
			if (messagecontent.errstack)
			{
				pd += "stacktrace :" + messagecontent.errstack + "\n";
			}
		}
		else
		{
			pd = "stacktrace : " + messagecontent.errstack + "\n";
		}
		
		if (browser)
		{
			for (bk in browser)
			{
				if (typeof(browser[bk]) == "function")
					continue;
					
				binfo += bk + ": " + browser[bk] + "\n";
			}
			
			pd = binfo + "\n\n" + pd;
			messagecontent.binfo = binfo;
		}
	}
		
	bbox = $("<div class='igc-alert'><a class='close'>&#215;</a><div class='detail' style='display: none'>" + IRm$.r1("M_DETAIL") + "</div><div class='cnt' style='display:none'></div><h4 class='alert-heading'>" + h + "</h4><span>" + msg + "</span></div>").appendTo(mbody);
	bclose = $(".close", bbox)[btn ? "hide" : "show"]();
	bdetail = $(".detail", bbox);
	
	pd && bdetail.show();
	
	if (!IG$.ErrorDialog)
	{
		bdetail.hide();
	}
	
	bbox.addClass("igc-alert-" + mtype);
	
	bdetail.bind("click", function(e) {
		e.preventDefault();
		e.stopImmediatePropagation();
		
		var doc = $(document),
			dw = doc.width(),
			dh = doc.height(),
			mw = Math.min(dw - 20, 600),
			mh = Math.min(dh - 20, 520);
			
		var msgpop = new IG$.ErrorDialog({
			messagecontent: messagecontent,
			width: mw,
			height: mh,
			renderTo: msgbox[0]
		});
		
		msgpop.setPosition((msgbox.width() - (msgpop.width || msgpop.$width)) / 2, (msgbox.height() - (msgpop.height || msgpop.$height)) / 2);
		// msgpop.setSize(msgpop.width, msgpop.height);
		msgpop.show();
	});
	
	bclose.bind("click", function(e) {
		e.preventDefault();
		e.stopImmediatePropagation();
		msgpop && msgpop.close();
		msgbox.fadeOut(function() {
			if (fn)
			{
				fn.call(parent);
			}
			msgbox.remove();
			
			if (is_error)
				IG$._berr = 0;
		});
	});
	
	if (!btn)
	{
		bbox.bind("click", function(e) {
			e.preventDefault();
			e.stopImmediatePropagation();
			msgpop && msgpop.close();
			msgbox.fadeOut(function() {
				if (fn)
				{
					fn.call(parent);
				}
				msgbox.remove();
				
				if (is_error)
					IG$._berr = 0;
			});
		});
	}
	else
	{
		var dbtn,
			dbtnsul,
			btns = [];
		
		if (btn == 6 || btn == 14)
		{
			btns.push({
				text: IRm$.r1("B_YES"),
				bseq: 2,
				dlg: "yes"
			});
			
			btns.push({
				text: IRm$.r1("B_NO"),
				bseq: 4,
				dlg: "no"
			});
			
			if (btn == 14)
			{
				btns.push({
					text: IRm$.r1("B_CANCEL"),
					bseq: 8,
					dlg: "cancel"
				});
			}
		}
		else if (btn == 1 || btn == 9)
		{
			btns.push({
				text: IRm$.r1("B_OK"),
				bseq: 1,
				dlg: "ok"
			});
			
			if (btn == 9)
			{
				btns.push({
					text: IRm$.r1("B_CANCEL"),
					bseq: 8,
					dlg: "cancel"
				});
			}
		}
		
		dbtn = $("<div class='igc-alert-buttons'><ul></ul></div>").appendTo(bbox);
		dbtnsul = $("ul", dbtn);
		
		$.each(btns, function(i, m) {
			var dp = $("<li></li>").appendTo(dbtnsul);
				d = $("<div class='igc-button'>" + m.text + "</div>").appendTo(dp);
				
			d.bind("click", function(e) {
				e.preventDefault();
				e.stopImmediatePropagation();
				msgpop && msgpop.close();
				msgbox.fadeOut(function() {
					if (fn)
					{
						fn.call(parent, m.dlg);
					}
					msgbox.remove();
					
					if (is_error)
						IG$._berr = 0;
				});
			});
		});
	}
	
	if (!ismodal)
	{
		msgbox.addClass("igc-no-modal");
		
		setTimeout(function() {
			msgpop && msgpop.close();
			
			msgbox.fadeOut(function() {
				if (fn)
				{
					fn.call(parent);
				}
				msgbox.remove();
				
				if (is_error)
					IG$._berr = 0;
			});
		}, 1500);
	}
}

