/**
 * common function to check object is locked from server
 * @memberof module:common/mainapp_webix
 */
IG$.checkLock = function(panel, frun) {
	var creq = new IG$._rpc$();
	creq.init(panel, 
		{
			ack: "helpcontent",
			payload: {uid: panel.uid},
			mbody: {option: 'lock', detail: "check"}
		}, panel, function(jdoc) {
			if (jdoc)
			{
				var lwin = new IG$.LockWindow({
					lparam: jdoc,
					callback: new IG$.callBackObj(panel, function(cmd) {
						if (cmd == "unlock")
						{
							frun.call(panel, cmd);
						}
						else
						{
							frun.call(panel, cmd);
						}
					})
				});
				lwin.show(panel);
			}
			else
			{
				var lreq = new IG$._rpc$();
				lreq.init(panel, 
					{
						ack: "helpcontent",
						payload: {uid: panel.uid},
						mbody: {option: 'lock', detail: "set"}
					}, panel, function(jdoc) {
						
					}, false);
					
				lreq.send();
			}
		}, false);
		
	creq.send();
}

if (window.$s)
{
	IG$.v$pnl = $s.extend($s.panel, {
		frameHeader: false,
		
		initComponent: function() {
			this.on("destroy", function(tobj, eopts) {
				tobj.__dx = true;
			});
			
			IG$.v$pnl.superclass.initComponent.call(this);
		},
		
		destroy: function() {
			var me = this;
			me.__dx = true;
			setTimeout(function() {
				IG$.v$pnl.superclass.destroy.call(me);
			}, 10);
		},
		
		setLoading: function(load, targetEl, bcustom) {
			var me = this,
				config = {};
			
			clearTimeout(me.__etimer);
			
			if (me.rendered && load == false)
			{
				me.__etimer = setTimeout(function() {
					if (me.customLoad && bcustom !== true)
					{
						me.customLoad(false);
					}
					me.loadMask && me.loadMask.destroy();
					me.rendermask = null;
					me.loadMask = null;
				}, 100);
			}
				
			if (me.rendered) 
			{
				if (load !== false) 
				{
					if (IG$.isObject(load)) 
					{
						IG$.apply(config, load);
					} 
					else if (IG$.isString(load)) 
					{
						config.msg = load;
					}
					
					config.dom = $(me.el.dom);
					config.target = me;
					
					if (!me.loadMask)
					{
						me.loadMask = new IG$._mask(config);
						me.loadMask.show();
					}
				}
			}
			return me.loadMask;
		}
	});
	
	IG$.LockWindow = $s.extend($s.window, {
		modal: true,
		region:'center',
		
		layout: 'fit',
		
		closable: false,
		resizable:false,
		
		width: 300,
		height: 180,
		
		callback: null,
		
		_closedlg: function(cmd) {
			this.callback && this.callback.execute(cmd);
			this.close();
		},
		
		initComponent : function() {
			var panel = this;
			
			panel.title = IRm$.r1('M_LOCK_TITLE');
			
			$s.apply(this, {
				defaults:{bodyStyle:'padding:10px'},
				
				items: [
					{
						xtype: "html",
						name: "txtlock",
						padding: 10,
						html: "<div class='igc-lockinfo'></div>"
					}
				],
				
				buttons:[
					{
						text: IRm$.r1('B_UNLOCK'),
						handler: function() {
							var lreq = new IG$._rpc$();
							lreq.init(panel, 
								{
									ack: "helpcontent",
									payload: {uid: panel.uid},
									mbody: {option: 'lock', detail: "unlock"}
								}, panel, function(jdoc) {
									panel._closedlg("unlock");
								}, false);
								
							lreq.send();
						},
						scope: this
					},
					{
						text: IRm$.r1('B_CLOSE'),
						handler: function() {
							this._closedlg("close");
						},
						scope: this
					}
				],
				
				listeners: {
					afterrender: function(ui) {
						var txtlock = this.down("[name=txtlock]"),
							lparam = this.lparam || {},
							msg,
							dt = Number(lparam.date),
							ddt = new Date(dt),
							fdt = "<br/><span class='igc-lock-date'>" + ddt.format("Y-m-d H:i;s") + "</span>";
							
						this.uid = lparam.uid;
						
						msg = IRm$.r1("M_LOCK_MSG", ["<span class='igc-lock-user'>" + lparam.username + "(" + lparam.userid + ")</span>", fdt]);
						$(".igc-lockinfo", $(txtlock.body.dom)).html(msg);
					}
				}
			});
			
			IG$.LockWindow.superclass.initComponent.apply(this, arguments);
		}
	});
	
	
	IG$.ErrorDialog = $s.extend($s.panel, {
		modal: true,
		
		layout: 'fit',
		
		closable: false,
		resizable:false,
		callback: null,
	
		_cerr: 0,
		
		showError: function(msg) {
			var me = this,
				
				param = "",
				k;
			
			if (msg.params)
			{
				for (k in msg.params)
				{
					if (param)
					{
						param += "\n";
					}
					
					param += k + ": " + msg.params[k];
				}
				
				param += "\n\n";
			}
			
			var m_msg = me.down("[name=m_msg]"),
				mbody = $(".ing-error-dlg", $(m_msg.body.dom));
				
			mbody.empty();
			
			if (IG$._berr && IG$._berr.err.length > 1)
			{
				$("<div class='ing-error-cnt'>(" + (me._cerr + 1) + "/" + IG$._berr.err.length + " messages)</div>").appendTo(mbody);
			}
			
			mbody.append("<div class='ing-error-desc'>" + (msg.errcode || "") + ":" + msg.errdesc + "</div>");
			mbody.append("<div class='ing-error-msg'>" + (msg.errmsg || "") + "</div>");
			
			$("<div class='ing-error-stack'></div>").appendTo(mbody).text(msg.errstack || "");
			$("<div class='ing-error-stack'></div>").appendTo(mbody).text(param || "");
			$("<div class='ing-error-stack'></div>").appendTo(mbody).text(msg.binfo || "");
			
			me.cboard = $("<textarea class='ing-error-cboard'></textarea>").appendTo(mbody).hide();
		},
		
		initComponent : function() {
			var me = this;
			
			$s.apply(this, {
				title: IRm$.r1("L_SERVER_MESSAGE"), // "Server Message",
				items: [
					{
						xtype: "html",
						name: "m_msg",
						border: 0,
						html: "<div class='ing-error-dlg'></div>"
					}
				],
				
				buttons: [
					{
						text: IRm$.r1("B_CLIPBOARD"),
						handler: function() {
							me.cboard.show();
							
							var _desc = function(msg) {
								var d = "",
									k, param = "";
									
								d = (msg.errdesc || "no description") + "\n";
								
								if (msg.params)
								{
									for (k in msg.params)
									{
										if (param)
										{
											param += "\n";
										}
										
										param += k + ": " + msg.params[k];
									}
									
									param += "\n\n";
								}
								else
								{
									param = "stacktrace : " + (msg.errstack || "") + "\n";
								}
								
								d += param;
								
								return d;
							};
							
							var tmessage = [];
							
							if (IG$._berr && IG$._berr.err.length)
							{
								$.each(IG$._berr.err, function(i, err) {
									tmessage.push(_desc(err.messagecontent));
								});
							}
							else
							{
								tmessage.push(_desc(me.messagecontent));
							}
							
							var binfo = "", browser = window.bowser;
							
							if (browser)
							{
								for (bk in browser)
								{
									if (typeof(browser[bk]) == "function")
										continue;
										
									binfo += bk + ": " + browser[bk] + "\n";
								}
								
								tmessage.push(binfo);
							}
							
							me.cboard.text(tmessage.join("\n\n"));
							
							me.cboard.select();
	
							try 
							{
								var successful = document.execCommand('copy');
								var msg = successful ? 'successful' : 'unsuccessful';
								console.log('Copying text command was ' + msg);
							} 
							catch (err) 
							{
								console.log('Oops, unable to copy');
							}
						},
						scope: this
					},
					"-",
					{
						text: IRm$.r1("B_PREVIOUS"),
						hidden: IG$._berr && IG$._berr.err.length > 1 ? false : true,
						handler: function() {
							var me = this,
								errs = IG$._berr.err,
								// l = errs.length,
								msg = me._cerr - 1 > -1 ? errs[me._cerr--] : null;
								
							if (msg)
							{
								me.showError(msg.messagecontent);
							}
						},
						scope: this
					},
					{
						text: IRm$.r1("B_NEXT"),
						hidden: IG$._berr && IG$._berr.err.length > 1 ? false : true,
						handler: function() {
							var me = this,
								errs = IG$._berr.err,
								l = errs.length,
								msg = me._cerr + 1 < l ? errs[me._cerr++] : null;
								
							if (msg)
							{
								me.showError(msg.messagecontent);
							}
						},
						scope: this
					},
					"-",
					{
						text: IRm$.r1("B_CLOSE"),
						handler: function() {
							this.close();
						},
						scope: this
					}
				],
				listeners: {
					afterrender: function(tobj) {
						var msg = me.messagecontent;
						
						me.showError(msg);
					}
				}
			});
			
			IG$.ErrorDialog.superclass.initComponent.apply(this, arguments);
		}
	});
}

IG$._scrshot = function(d, bind, enable, immediate) {
	var scobj,
		// t, 
		m, n,
		toff,
		tx, ty,
		bd = $("body"),
		bw = bd.width(),
		bh = bd.height();
		
	if (ig$.report_screenshot === false || ig$.$l_ > 5)
		return;
	
	scobj = IG$.__scrs = IG$.__scrs || {};
	
	if (scobj._close_timer)
		clearTimeout(scobj._close_timer);
	
	scobj._close_timer = -1;
	
	if (!enable)
	{
		if (immediate)
		{
			if (scobj.container)
			{
				scobj.container.hide();
			}
		}
		else
		{
			scobj._close_timer = setTimeout(function() {
				if (scobj.container)
				{
					scobj.container.hide();
				}
			}, 2000);
		}
		return;
	}
	
	if (!scobj.container)
	{
		scobj.container = $("<div class='igc-screenshot-cont'><div class='igc-screenshot-img'></div></div>").appendTo($("body"));
		scobj.img = $(".igc-screenshot-img", scobj.container);
		
		scobj.container.bind("click", function() {
			clearTimeout(scobj._close_timer);
			scobj._close_timer = -1;
			
			scobj.container.hide();
		});
		
		scobj._w = scobj.container.width();
		scobj._h = scobj.container.height();
		
		$("body").bind("click", function() {
			scobj.container.hide();
		});
	}
	
	scobj.container.show();
	
	n = bind.indexOf(":");
	t = bind.substring(0, n);
	m = bind.substring(n+1);
	
	if (scobj.__curuid != m)
	{
		toff = $(d).offset();
		
		scobj._curuid = m;
		
		scobj.img.css({
			backgroundImage: "url('" + ig$.servlet + "?sreq=screenshot&uid=" + m + "&uniquekey=" + IG$.getUniqueKey() + "')",
		});
		
		ty = toff.top;
		
		if (ty + scobj._h > bh)
		{
			ty = toff.top - scobj._h;
		}
		
		tx = toff.left + $(d).width();
		
		if (tx + scobj._w > bw)
		{
			tx = toff.left - scobj._w;
		}
		
		scobj.container[0].style.top = "" + ty + "px";
		scobj.container[0].style.left = "" + tx + "px";
	}
	
	scobj._close_timer = setTimeout(function() {
		if (scobj.container)
		{
			scobj.container.hide();
		}
	}, 2000);
}

