IG$._mask = function(opt) {
	var me = this,
		bbox,
		mbox;
	
	me.owner = opt.target;
	
	me.mask = $("<div class='igc-loading'></div>");
	
	bbox = me.make_dom(me.mask);
	
	mbox = $(".igc-loading-msg", bbox);
	
	mbox.append(opt.msg || IRm$.r1("L_LOADING"));
	
	me.owner.rendermask = me.mask;
	if (opt.dom)
	{
		opt.dom.append(me.mask);
	}
	else
	{
		me.owner._el.dom.append(me.mask);
	}
}

IG$._mask.prototype = {
	make_dom: function(mask) {
		var me = this,
			lmask, lbody, bbox;
			
		lmask = $("<div class='igc-loading-mask'></div>").appendTo(mask);
		lbody = $("<table class='igc-loading-body'><tr><td></td></tr></table>").appendTo(mask);
		bbox = $("<div class='igc-loading-box'><table><tr><td><div class='igc-loading-proc'><div class='spinner'><div class='rect1'></div><div class='rect2'></div><div class='rect3'></div><div class='rect4'></div><div class='rect5'></div></div></div></td><td><span class='igc-loading-msg'></span></td></tr></table></div>")
			.appendTo($("td", lbody));
			
		return bbox;
	},
	show: function() {
		var me = this;
		me.mask.show();
	},
	destroy: function() {
		var me = this;
		
		if (me.mask)
		{
			me.mask.remove();
			me.mask = null;
		}
	}
}

