/**
 * check session validity
 * @memberof module:framework/login
 * @param sccall {object} callback on success
 * @param scfaile {object} callback on fail
 */
IG$.chkSess = function(sccall, fail) {
	var panel = this,
		nmts,
		url_params;
		
	if (!IG$._g$a)
	{
		url_params = IG$._parse_url().params;
		nmts = url_params["mts"];
	}
	
	var do_session = function() {
		var lreq = new IG$._rpc$();
		
		lreq.init(panel,
			{
				ack: "loginstatus",
				payload: {},
				mbody: {lang: useLocale, app: loadingApp}
			}, panel, function(jdoc) {
				var ltoken = jdoc.token,
					nuid = jdoc.uid,
					redirect = jdoc.redirect;
				
				if (sccall)
				{
					window.$sso_try_count = 0;
					sccall.call();
				}
				else if (redirect && redirect.length > 0)
				{
					window.$sso_try_count = 0;
					window.location.replace(redirect);
				}
			}, function(jdoc) {
				fail && fail.call();
				
				return false;
			});
	
		lreq.send();
	}
	
	if (nmts && nmts.length)
	{
		var lreq = new IG$._rpc$();
		
		lreq.init(panel,
			{
				ack: "security",
				payload: {mts: nmts || ""},
				mbody: {}
			}, panel, function(item) {
				var p1 = item.p1,
					p2 = item.p2;
				
				if (p1 && p2)
				{
					IG$.rsaPublicKeyModules = p1;
					IG$.rsaPublicKeyExpoenent = p2;
					IG$.rsa_kinfo = item.p3;
					IG$._g$a = item.mts;
					
					do_session();
				}
				else
				{
					do_session();
				}
			}, function() {
				do_session();
			});
	
		lreq.send();
	}
	else
	{
		do_session();
	}
}

/**
 * parse url for parameter
 * @memberof module:framework/login
 */
IG$._parse_url = function(url) {
	var curl = url || $(location).attr('href'),
		params = [],
		param_map = {},
		param_tag = {},
		burl,
		amap,
		mtype;
	
	burl = curl;
	
	if (curl.indexOf("#") > -1)
	{
		amap = curl.substring(curl.lastIndexOf("#") + 1);
		curl = curl.substring(0, curl.lastIndexOf("#"));
		burl = curl;
		
		params = amap.split("&");
		
		$.each(params, function(i, p) {
			var k, v;
			if (p && p.indexOf("=") > 0)
			{
				k = p.substring(0, p.indexOf("="));
				v = p.substring(p.indexOf("=") + 1);
				
				v = decodeURIComponent(v);
				
				param_tag[k] = v;	
			}
		});
	}
	
	if (curl && curl.indexOf("?") > 0)
	{
		burl = curl.substring(0, curl.indexOf("?"));
		curl = curl.substring(curl.indexOf("?") + 1);
		
		params = curl.split("&");
		
		$.each(params, function(i, p) {
			var k, v;
			if (p.indexOf("=") > 0)
			{
				k = p.substring(0, p.indexOf("="));
				v = p.substring(p.indexOf("=") + 1);
				
				v = decodeURIComponent(v);
				
				param_map[k] = v;
			}
		});
	}
	
	if (burl && burl.indexOf(".") > -1)
	{
		mtype = burl.substring(burl.lastIndexOf(".") + 1);
	}
	
	return {
		doctype: mtype,
		url: burl,
		params: param_map,
		params_tag: param_tag
	};
}

IG$._unstrip_url = function(url) {
	var r = url.url,
		n = 0;
	
	if (url.params)
	{
		$.each(url.params, function(i, param) {
			if (n == 0)
			{
				r += "?";
			}
			else
			{
				r += "&";
			}
			
			r += i + "=" + (param || "");
			n++;
		});
	}
	
	if (url.params_tag)
	{
		n = 0;
		$.each(url.params_tag, function(i, param) {
			if (n == 0)
			{
				r += "#";
			}
			else
			{
				r += "&";
			}
			
			r += i + "=" + (param || "");
			n++;
		});
	}
	
	return r;
}

/**
 * start session process with userid password
 * @memberof module:framework/login
 * @param userid {string} userid
 * @param passwd {string} password
 * @param bg {dom} background overflow
 * @param mts {string} multi tenant id
 * @param callback {object} error callback
 */
IG$.doStartSession = function(userid, passwd, bg, mts, errcallback) {
	if (bg)
	{
		bg.show();
	}
	
	var panel = this,
		il_err = $("#il_err");
		
	il_err.hide();

	var lreq = new IG$._rpc$();
	lreq.init(panel,
		{
			ack: "security",
			payload: {mts: mts},
			mbody: {}
		}, panel, function(item) {
			var p1, p2;
			
			p1 = item.p1;
			p2 = item.p2;
			
			if (p1 && p2)
			{
				IG$.rsaPublicKeyModules = p1;
				IG$.rsaPublicKeyExpoenent = p2;
				IG$.rsa_kinfo = item.p3;
				
				IG$._g$a = item.mts;
				
				setTimeout(function() {
					IG$.pLoginRSA(userid, passwd, bg, mts, errcallback);
				}, 10);
			}
			else
			{
				alert("Error while get security key");
				if (bg)
				{
					bg.hide();
				}
			}
			
		}, function(jdoc) {
			var errorcode = jdoc.errorcode,
				msg = jdoc.errormsg;
			
			if (bg)
			{
				bg.hide();
			}
			
			if (errorcode == "0x6d00")
			{
				il_err.unbind("click");
				il_err.bind("click", function() {
					il_err.hide();
				});
				il_err.show();
				$(".igc-errorinfo-msg", il_err).html(msg);

				return false;
			}
		});

	lreq.send();
}

IG$.psst_set = function(cls, skey) {
	if (cls && window.use_session_key && skey)
	{
		$.cookie("amp_auth", skey);
	}
	else
	{
		$.removeCookie("amp_auth");
		ig$._session_key = null;
	}
};

/**
 * RSA login session with encryption
 * @memberof module:framework/login
 * @param userid {string} userid
 * @param passwd {string} password
 * @param bg {dom} background overflow
 * @param mts {string} multi tenant id
 * @param callback {object} error callback
 */
IG$.pLoginRSA = function(userid, passwd, bg, mts, errcallback)
{
	var encpwd = IG$.encryptKey([userid, passwd]); // CryptoJS.SHA1(passwd);
	// passwd = encpwd.toString(); //(CryptoJS.enc.Hex);
	
	var il_err = $("#il_err"),
		panel = this;
		
	il_err.hide();
	
	IG$.psst_set(0);
	
	var lreq = new IG$._rpc$();
	lreq.init(panel,
		{
			ack: "login",
			payload: {userid: encpwd[0], passwd: encpwd[1], keyvar: IG$.rsa_kinfo},
			mbody: {lang: useLocale, app: loadingApp, theme: ig$.theme_id, session_expire: "" + (ig$.session_expire || 0), mts: (mts || "")}
		}, panel, function(item) {
			var ltoken,
				nuid,
				redirect;
			
			if (bg)
			{
				bg.hide();
			}
			
			ltoken = item.token;
			nuid = item.uid;
			
			redirect = item.redirect;
			
			if (redirect && redirect.length > 0)
			{
				window.location.replace(redirect);
			}
			else
			{
				alert("System Error:: Application is not defined on configuration!");
			}
		}, function(jdoc) {
			var msg = jdoc.errormsg,
				errorcode = jdoc.errorcode;
				
			if (bg)
			{
				bg.hide();
			}
					
			if (errorcode == "0x6d00")
			{
				il_err.unbind("click");
				il_err.bind("click", function() {
					il_err.hide();
				});
				il_err.show();
				$(".igc-errorinfo-msg", il_err).html(msg);
				
				errcallback && errcallback.execute();

				return false;
			}
			else if (errcallback)
			{
				errcallback.execute();

				return false;
			}
		});

	lreq.send();
}

/**
 * set server information text
 * @memberof module:framework/login
 */
IG$.chkSvrInfo = function(callback) {
	var panel = this,
		lreq = new IG$._rpc$();
	lreq._rmethod = "GET";
	lreq.init(panel,
		{
			get_params: {sreq: "version"}
		}, panel, function(res, jdoc) {
			ig$.appInfo = ig$.appInfo || {};
			ig$.appInfo.appversion = ig$.appInfo.appversion$ || res.version || "0.0";
			ig$.appInfo.apprelease = ig$.appInfo.apprelease$ || res.release || "0.00";
			ig$.appInfo.licensed = res.licensed || "xxx";
			callback && callback.call();
		}, function(jdoc) {
			ig$.appInfo = ig$.appInfo || {};
			ig$.appInfo.appversion = "_._";
			ig$.appInfo.apprelease = "_._.___";
			ig$.appInfo.licensed = "";
			callback && callback.call();

			return false;
		});

	lreq.send();
}

/**
 * create login div for session timeout
 * @memberof module:framework/login
 */
IG$.createLoginPanel = function(d1, d2, addevent){
	var loginWindow = $("<div id='loginWindow' class='loginWindow'></div>"),
		logincnt = $("<div class='login-container'></div>").appendTo(loginWindow),
		i,
		lng = ig$.lang, l,
		themes = ig$.themes,
		browser = window.bowser;
		
	if (browser.msie)
	{
		loginWindow.css({width: "100%", height: "100%"});
		if (ig$.appbg)
		{
			var bg = $("<img src='./images/" + (ig$.appbg) + "' class='background'/>");
			
			bg.css({
				width: "100%", height: "100%", position: "absolute", top: 0, left: 0
			});
			logincnt.append(bg);
		}
	}
	
	if (lng)
	{
		for (i=0; i < lng.length; i++)
		{
			if (lng[i].code == useLocale)
			{
				l = lng[i];
				break;
			}
		}
	}
	
	// var loginBox = $("<div class='loginBox'></div>").appendTo(logincnt);
	var lf;
	
	lf = [
		"<div class='login-mc bounceInDown animated'>",
		"<div class='login-ic'>",
		"<img class='login-logo' src='./images/logo_7186.png'>",
	
		"<div id='login_locale'><div class='locale_button'><span>Language</span>", // <div class='selbutton'></div>"
		"<select id='b_loc'>"
	];
	
	if (lng)
	{
		for (i=0; i < lng.length; i++)
		{
			lf.push("<option value='" + lng[i].code + "'" + (useLocale == lng[i].code ? " selected" : "") + ">" + lng[i].disp + "</option>");
		}
	}
			
	lf.push("</select></div></div>");
	
	if (themes)
	{
		lf.push("<div id='login_theme'><div class='locale_button'><span>Theme</span>"); // <div class='selbutton'></div>"
		lf.push("<select id='b_theme'>");
		
		for (i=0; i < themes.length; i++)
		{
			lf.push("<option value='" + themes[i].code + "'" + (ig$.theme_id == themes[i].code ? " selected" : "") + ">" + themes[i].disp + "</option>");
		}
		
		lf.push("</select></div></div>");
	}
			
	lf.push("<form name='login_form' id='login_form' rel='nofollow' target='temp' action=''>");
	lf.push(" <ul id='login_path'>");
	lf.push("   <li>");
	lf.push("	<div id='user_lbl' class='login-lbl'>" + ((l && l.l1) ? l.l1 : "User ID") + "</div>");
	lf.push("	<input id='userid' type='text' name='userid' value='" + (d1 || "") + "'>");
	lf.push("   </li>");
	lf.push("   <li>");
	lf.push("	<div id='pass_lbl' class='login-lbl'>" + ((l && l.l2) ? l.l2 : "Password") + "</div>");
	lf.push("	<input id='userpassword' type='password' name='userpassword' value='" + (d2 || "") + "'>");
	lf.push("   </li>");
	lf.push("   <li>");
	lf.push("	<div class='login-lbl'>&nbsp;</div>");
	lf.push("	<input id='login_btn' type='button' value='" + ((l && l.l3) ? l.l3 : "Login") + "'>");
	lf.push("   </li>");
	lf.push(" </ul>");
	lf.push(" <ul id='twoway_form' style='display:none;'>");
	lf.push("   <li>");
	lf.push("	<div id='user_lbl' class='login-lbl'>" + ((l && l.l5) ? l.l5 : "2 way key") + "</div>");
	lf.push("	<input id='twoway_key' type='text' name='twoway_key' value=''>");
	lf.push("   </li>");
	lf.push("   <li>");
	lf.push("	<div class='login-lbl'>&nbsp;</div>");
	lf.push("	<input id='twoway_validate' type='button' value='" + ((l && l.l6) ? l.l6 : "Validate") + "'>");
	lf.push("   </li>");
	lf.push("   <li>");
	lf.push("	<div class='login-lbl'>&nbsp;</div>");
	lf.push("	<input id='twoway_retry' type='button' value='" + ((l && l.l7) ? l.l7 : "Retry") + "'>");
	lf.push("   </li>");
	lf.push(" </ul>");
	lf.push("</form>");
	
	lf.push("<div id='license'>Licensed to: " + (ig$.appInfo ? ig$.appInfo.licensed || "xxx" : "...") + "</div>");

	lf.push("</div>"); // login-ic
	lf.push("</div>");
	  
	loginForm = $(lf.join("")).appendTo(logincnt).addClass("loginForm");
	  
	var legal = $("<div class='legal'>"
		+ "<table border='0' cellpadding='0' cellspacing='0'>"
		+ "  <tr>"
		+ "	<td width='78'>&nbsp;</td>"
		+ "	<td width='*' valign='top'><div align='left'>"
		+ "	  <p class='style2'>" + ig$.copy + "</p>"
		+ "	  <p class='style1'><span class='style4'>For support please contact</span>"
		+ "	  <a href='https://amplixbi.com' target='_new'>amplixbi.com</a></p>"
		+ "	</div></td>"
		+ "  </tr>"
		+ "</table></div>").appendTo(logincnt);

	var build = $("<div class='build'>"
		+ "<div align='right'><span class='style6'>Build Id: <span id='app_release'" + (ig$.appInfo && ig$.appInfo.apprelease) + "</span></span></div>"
		+ "</div>").appendTo(logincnt);
		
	$("<div id='il_err' class='igc-alert-cnt'><div class='igc-alert-bg'></div><div class='igc-alert-body'><div class='igc-alert igc-alert-error'><a class='close'>x</a><h4 class='alert-heading'></h4><span class='igc-errorinfo-msg'></span></div></div></div>")
		.appendTo(logincnt)
		.hide();
	
	var progress = $("<div id='login-progress' class='login-progress'></div>")
		.css({position: "absolute", width:"100%", height: "100%", top: 0, left: 0, right: 0, bottom: 0})
		.hide().appendTo(logincnt);
	
	if (window.IG$.cLogin)
	{
		window.IG$.cLogin(loginWindow);
	}
	
	$(document.body).append(loginWindow);
	
	IG$.chkSvrInfo(function() {
		$("#app_release", build).text(ig$.appInfo.apprelease);
		$("#license", loginWindow).text("Licensed to: " + (ig$.appInfo.licensed || "xxx"));
	});
	
	setTimeout(function() {
		if (d1)
		{
			$("#userpassword").focus();
		}
		else
		{
			$("#userid").focus();
		}
	}, 200);
	
	$("#userpassword").bind("focus", function() {
		$("#userpassword").select();
	});
	
	$("#userid").bind("focus", function() {
		$("#userid").select();
	});
	
	$("#userpassword").bind("mouseup", function() {
		setTimeout(function() {
			$("#userpassword").select();
		}, 80);
	});
	
	$("#userid").bind("mouseup", function() {
		setTimeout(function() {
			$("#userid").select();
		}, 80);
	});
	
	if (addevent !== false)
	{
		$("#b_loc").bind("change", function(e) {
			var b_loc = $("#b_loc"),
				selvalue = $("option:selected", b_loc).val(),
				redirect = $(location).attr('href'),
				urlinfo;
			
			if (selvalue && selvalue != window.useLocale)
			{
				$.removeCookie("lang");
				$.cookie("lang", selvalue, {
					path: "/"
				});
				
				urlinfo = IG$._parse_url(redirect);
				if (urlinfo.doctype == "html")
				{
					urlinfo.params_tag.lang = selvalue;
				}
				else
				{
					urlinfo.params.lang = selvalue;
				}
				
				urlinfo.params_tag.source = "app";
				redirect = IG$._unstrip_url(urlinfo);
				
				progress.show();
				
				setTimeout(function() {
					window.location.replace(redirect);
					if (urlinfo.doctype == "html")
					{
						window.location.reload();
					}
				}, 100);
			}
		});
		
		$("#b_theme").bind("change", function(e) {
			var b_theme = $("#b_theme"),
				selvalue = $("option:selected", b_theme).val(),
				redirect = $(location).attr('href');
							
			if (selvalue != ig$.theme_id)
			{
				$.removeCookie("theme");
				$.cookie("theme", selvalue, {
					path: "/"
				});
				
				urlinfo = IG$._parse_url(redirect);
				
				if (urlinfo.doctype == "html")
				{
					urlinfo.params_tag.theme = selvalue;
				}
				else
				{
					urlinfo.params.theme = selvalue;
				}
				
				urlinfo.params_tag.source = "app";
				redirect = IG$._unstrip_url(urlinfo);
				
				progress.show();
				
				setTimeout(function() {
					window.location.replace(redirect);
					if (urlinfo.doctype == "html")
					{
						window.location.reload();
					}
				}, 100);
			}
		});
		
		$("#login_btn").bind("click", function(e) {
			e.preventDefault();
			e.stopPropagation();
			
			var userid = $("#userid").val(),
				passwd = $("#userpassword").val();
				
			$("#userpassword").val("");
			$(this).blur();
			
			if (!userid)
			{
				IG$.ShowError(IRm$.r1("L_BLANK_USERID"));
				return false;
			}
			
			if (!passwd)
			{
				IG$.ShowError(IRm$.r1("L_BLANK_PASSWORD"));
				return false;
			}
			
			IG$.dlgLogin.getKeyPair.call(IG$.dlgLogin, userid, passwd, progress, window.m$mts);
			return false;
		});
		
		$("#userpassword").bind("keypress", function(e) {
			if (e.keyCode == 13)
			{
				var userid = $("#userid").val(),
					passwd = $("#userpassword").val();
					
				$("#userpassword").val("");
				$(this).blur();
				
				if (!userid)
				{
					IG$.ShowError(IRm$.r1("L_BLANK_USERID"));
					return false;
				}
				
				if (!passwd)
				{
					IG$.ShowError(IRm$.r1("L_BLANK_PASSWORD"));
					return false;
				}
				
				IG$.dlgLogin.getKeyPair.call(IG$.dlgLogin, userid, passwd, progress, window.m$mts);
				return false;
			}
			
			return true;
		});
	}
	
	return loginWindow;
}

/**
 * show login section for invalide session or timeout
 * @memberof module:framework/login
 */
IG$.showLogin = function(callback, rs)
{
	$("#idv-mnu-pnl").hide();
	
	if (IG$.dlgLogin)
	{
		IG$.dlgLogin.callback = new IG$.callBackObj(this, function() {
			IG$.dlgLogin.tl = -1;
			IG$.showLoginProc();
		});
	}
	
	if (window.hist)
	{
		window.hist.addHistory("");
	}

	// close all windows
	if (IG$._popupDlgs)
	{
		$.each(IG$._popupDlgs, function(i, dlg) {
			try
			{
				if (dlg && dlg.close)
				{
					dlg.close();
				}
			}
			catch (e)
			{
			}
		});

		IG$._popupDlgs = [];
	}
	
	var lform = $("#loginWindow"),
		browser = window.bowser;

	$("#license", lform).html("Licensed to: " + (ig$.appInfo ? ig$.appInfo.licensed || "xxx" : "..."));
		
	lform.css({zIndex: 99});
	if (browser.msie)
	{
		lform.css({position: "absolute", top: 0, left: 0, right: 0, bottom: 0, width: "100%", height: "100%", margin: 0, padding: 0});
	}
	lform.show();
}

/**
 * logout action
 * @memberof module:framework/login
 */
IG$.showLogout = function(callback)
{
	if (IG$.msgint > -1)
	{
		clearInterval(IG$.msgint);
		IG$.msgint = -1;
	}
	
	IG$.confirmMessages(IRm$.r1("B_CONFIRM"), IRm$.r1("L_T_LOUT"), function(dlg) {
		if (dlg == "yes")
		{
			IG$.psst_set(0);
			
			var req = new IG$._rpc$();
			req.init(null, 
				{
					ack: "logout",
					payload: {},
					mbody: {}
				}, null, 
				function(xdoc){
					IG$.psst_set(0);
				},
				function(){
					return true;
			});
			
			req.send();
			
			IG$.showLogin(callback, 1);
		}
	});
}
