IG$.checkTimeout = function(session_timeout) {
	var timeout;
	
	if (session_timeout)
	{
		timeout = session_timeout + new Date().getTime();
		
		clearTimeout(IG$.Dtimer);
		clearTimeout(IG$.Wtimer);
		
		IG$.Wtimer = setTimeout(function() {
			var ctime = new Date().getTime() - 60 * 5;
			if (ctime >= timeout - 10) 
			{
				IG$.alertmsg(ig$.appname, "Your session is going to be end by 5 min, Please click OK to continue.", null, null, 0, "");
			}
		}, (session_timeout - 60*5) * 1000);
		
		IG$.Dtimer = setTimeout(function() {
			var ctime = new Date().getTime();
			if (ctime >= timeout - 10) 
			{
				IG$.showLogin(null, 2);
			}
		}, session_timeout * 1000);
	}
}

