if (!IG$.callBackObj)
{
	/**
	 * callback object definition
     * @memberof module:common/mainapp
     * @param callerptr {object} caller object with scope
     * @param callexec {function} callback function
     * @param callparam {object} parameter to callback function
	 */
	IG$.callBackObj = function(callerptr, callexec, callparam) {
		var me = this;
		me.p1 = callerptr;
		me.p2 = callexec;
		me.p3 = callparam;
	}
	
	IG$.callBackObj.prototype = {
		/**
		 * callback execution
	     * @memberof module:common/mainapp
		 */
		execute: function() {
			var me = this,
				arg = arguments,
				marg = [],
				i,
				ret;
			
			if (me.p2)
			{
				for (i=0; i < arg.length; i++)
				{
					marg.push(arg[i]);
				}
				if (me.p3)
				{
					marg.push(me.p3);
				}
				ret = me.p2.apply(me.p1 || null, marg);
			}
			
			return ret;
		}
	};
}

