IG$.override = function (target, overrides) {
	if (target.$isClass) {
		target.override(overrides);
	} else if (typeof target == "function") {
		IG$.apply(target.prototype, overrides);
	} else {
		var owner = target.self,
			name, value;

		if (owner && owner.$isClass) { // if (instance of Ext.defined class)
			for (name in overrides) {
				if (overrides.hasOwnProperty(name)) {
					value = overrides[name];

					if (typeof value == "function") {
						//<debug>
						if (owner.$className) {
							value.displayName = owner.$className + "#" + name;
						}
						//</debug>

						value.$name = name;
						value.$owner = owner;
						value.$previous = target.hasOwnProperty(name)
							? target[name] // already hooked, so call previous hook
							: callOverrideParent; // calls by name on prototype
					}

					target[name] = value;
				}
			}
		} else {
			IG$.apply(target, overrides);
		}
	}

	return target;
}

IG$.extend = (function() {
	// inline overrides
	var objectConstructor = Object.prototype.constructor,
		inlineOverrides = function(o) {
		for (var m in o) {
			if (!o.hasOwnProperty(m)) {
				continue;
			}
			this[m] = o[m];
		}
	};

	return function(subclass, superclass, overrides) {
		// First we check if the user passed in just the superClass with overrides
		if (IG$.isObject(superclass)) {
			overrides = superclass;
			superclass = subclass;
			subclass = overrides.constructor !== objectConstructor ? overrides.constructor : function() {
				return superclass.apply(this, arguments);
			};
		}

		// We create a new temporary class
		var F = function() {},
			subclassProto, superclassProto = superclass.prototype;
		F.prototype = superclassProto;
		subclassProto = subclass.prototype = new F();
		subclassProto.constructor = subclass;
		subclass.superclass = superclassProto;

		if (superclassProto.constructor === objectConstructor) {
			superclassProto.constructor = superclass;
		}

		subclass.override = function(overrides) {
			IG$.override(subclass, overrides);
		};

		subclassProto.override = inlineOverrides;
		subclassProto.proto = subclassProto;

		subclass.override(overrides);
		subclass.extend = function(o) {
			return IG$.extend(subclass, o);
		};

		return subclass;
	};
}());

IG$.microChartType = function(chartdata, opt) {
	var mctype = chartdata.mctype;
	
	switch(mctype)
	{
	case 0:
		opt.type = "bullet";
		break;
	case 2:  // area
		opt.type = "line";
		opt.lineColor = chartdata.linecolor || "#5151FF";
		opt.fillColor = chartdata.fillcolor || "#c0d0f0";
		opt.lineWidth = 1;
		break;
	case 3:
		opt.type = "line";
		opt.lineColor = chartdata.linecolor || "#5151FF";
		opt.fillColor = null;
		opt.lineWidth = 1;
		break;
	case 4:
		opt.type = "bar";
		opt.barColor = chartdata.linecolor || "#5151FF";
		opt.negBarColor = chartdata.linecolor || "#FF5151";
		break;
	case 6:
		opt.type = "box";
		break;
	case 7:
		opt.type = "tristate";
		opt.posBarColor = chartdata.linecolor || "#5151FF";
		opt.negBarColor = chartdata.linecolor || "#FF5151";
		break;
	case 8:
		opt.type = "pie";
		break;
	case 9:
		opt.type = "box";
		opt.boxFillColor = chartdata.linecolor || "#5151FF";
		opt.medianColor = chartdata.linecolor || "#FF5151";
		break;
	default: 
		opt.type = "line";
		opt.lineColor = chartdata.linecolor || "#5151FF";
		opt.fillColor = chartdata.fillcolor || "#fff";
		break;
	}
}

IG$.apply = function(object, config, defaults, overwrite) {
	if (object && config && typeof config === "object") {
		var i, j, k;

		for (i in config) {
			if (object.hasOwnProperty(i) == false || overwrite !== false)
			{
				object[i] = config[i];
			}
		}
	}

	return object;
};

IG$.defaults = function(object, defaults) {
	if (object && defaults && typeof defaults === "object") {
		var i, j, k;

		for (i in defaults) {
			if (typeof (object[i]) == 'undefined')
				object[i] = defaults[i];
		}
	}

	return object;
};

