/**
 * static function to sort meta items
 * @memberof module:common/mainapp
 */
IG$.sortMeta_old = function(items) {
	var torder = IG$._sortTypeOrder,
		i;
	
	for (i=0; i < items.length; i++)
	{
		items[i].ltype = items[i].type.toLowerCase();
		items[i].lfd = IG$.isFolder(items[i].ltype);
	}
	
	items.sort(function(a, b) {
		var c = 0,
			sa, sb,
			na = -1, nb = -1,
			al = a.lfd,
			bl = b.lfd,
			at = a.ltype,
			bt = b.ltype,
			an = a.nodepath || "",
			bn = b.nodepath || "",
			at1 = torder[at] || 99,
			bt1 = torder[bt] || 99,
			L, i, n, a1, b1;
			
		an = a.locale_name || a.name; // an.substring(0, an.lastIndexOf("/"));
		bn = b.locale_name || b.name; // bn.substring(0, bn.lastIndexOf("/"));
		
		if (at1 != bt1)
		{
			c = at1 - bt1;
		}
		else if (an != bn)
		{
			sa = an.toLowerCase().match(IG$._nsort);
			sb = bn.toLowerCase().match(IG$._nsort);
			
			L = sa.length;
			i = 0;
			
			while(i<L)
			{
				if(!sb[i])
				{
					c = 1;
					break;
				}
				a1= sa[i],
				b1= sb[i++];
				if(a1!== b1)
				{
					n= a1-b1;
					if(!isNaN(n))
					{
						c = n;
					}
					else
					{
						c = a1>b1? 1:-1;
					}
					break;
				}
			}
		}
		else if (al == bl && at1 == bt1)
		{
			an = a.lname || a.name;
			bn = b.lname || b.name;
			
			sa = an.toLowerCase().match(IG$._nsort);
			sb = bn.toLowerCase().match(IG$._nsort);
			
			L = sa.length;
			i = 0;
			
			while(i<L)
			{
				if(!sb[i])
				{
					c = 1;
					break;
				}
				a1= sa[i],
				b1= sb[i++];
				if(a1!== b1)
				{
					n= a1-b1;
					if(!isNaN(n))
					{
						c = n;
					}
					else
					{
						c = a1>b1? 1:-1;
					}
					break;
				}
			}
		}
		else if (al == bl)
		{
			c = at1 > bt1 ? -1 : 1;
		}
		else 
		{
			c = (al > bl) ? -1 : 1;
		}
		
		return c;
	});
}

IG$.get_datatype = function(typename) {
	var r = null;
	
	switch (typename)
	{
	case "12":
	case "1":
	case "-1":
	case "-15":
		r = "VARCHAR";
		break;
	case "5":
	case "4":
	case "-5":
	case "-6":
	case "-7":
	case "7":
	case "8":
	case "3":
	case "-2":
	case "-3":
	case "-4":
	case "2":
		r = "NUMERIC";
		break;
	case "91":
	case "92":
	case "93":
		r = "DATE";
		break;
	case "2004":
	case "2005":
		r = "LOB";
		break;
	default:
		r = "OTHER";
		break;
	}
	
	return r;
}

/**
 * meta type to icon
 * @memberof module:common/mainapp
 */
IG$.get_metatype_icon = function(typename, tobj) {
	var r = "",
		cache = IG$.getMetaItemCache.itemicon,
		tname = typename;
	
	if (typename == "link" && tobj && !tobj.rawtype && tobj.attributes && tobj.attributes.attrb2)
	{
		tobj.rawtype = tobj.attributes.attrb2;
	}
	
	var memo = tobj ? (tobj.datatype || "") + "_" + (tobj.rawtype || "") : ""; 
		
	if (cache[typename + "_" + memo])
	{
		r = cache[typename + "_" + memo];
	}
	else
	{
		if (typename == "link" && tobj && tobj.rawtype)
		{
			tname = tobj.rawtype.toLowerCase();
		}
		
		switch (tname)
		{
		case "metrics":
			r = "icon-folder";
			break;
		case "datacube":
			r = "icon-excel";
			break;
		default:
			r = "icon-" + tname;
		
			if (tname == "metric" && tobj && tobj.datatype)
			{
				r += "_" + tobj.datatype;
			}
			break;
		}
		
		if (typename == "link")
		{
			r = "icon-link-disp " + r;
		}
		
		cache[typename + "_" + memo] = r;
	}
	
	return r;
}

IG$.findSubNode = function(unode, nodename, nodevalue) {
	var cdiv = null;
	if (unode.childNodes != null && unode.childNodes.length > 0)
	{
		var i;
		for (i=0; i < unode.childNodes.length; i++)
		{
			var cvalue = (unode.childNodes[i].getAttribute) ? unode.childNodes[i].getAttribute(nodename) : null;
			
			if (cvalue && cvalue == nodevalue)
			{
				return unode.childNodes[i];
			}
			
			if (unode.childNodes[i].childNodes && unode.childNodes[i].childNodes.length > 0)
			{
				cdiv = IG$.findSubNode(unode.childNodes[i], nodename, nodevalue);
				
				if (cdiv != null)
					return cdiv;
			}
		}
	}
	
	return cdiv;
}

/**
 * xml related
 */
IG$.loadXML = function(doc) {
	var xdoc,
		parser;
	
	if (doc.charAt(0).charCodeAt(0) == 10)
	{
		doc = doc.substring(1);
	}
   
	if (doc.charAt(0) != "<")
	{
		doc = Base64.decode(doc);
	}
		
	if (window.DOMParser)
	{
		parser = new DOMParser();
		xdoc = parser.parseFromString(doc, "application/xml");
	}
	else
	{
		xdoc = new ActiveXObject("Microsoft.XMLDOM");
		xdoc.async = false;
		xdoc.loadXML(doc);
	}
	
	return xdoc;
}

