/**
 * color interpolation
 * @memberof module:common/mainapp
 * @param minColor {string} minimum value color
 * @param maxColor {string} maximum value color
 * @param maxDepth {integer} maximum depth of colors
 * @param depth {integer} depth of colors
 */
IG$.interpolateColor = function(minColor,maxColor,maxDepth,depth){
	
	function d2h(d) {return d.toString(16);}
	function h2d(h) {return parseInt(h,16);}
   
	if(depth == 0){
		return minColor;
	}
	if(depth == maxDepth){
		return maxColor;
	}
   
	var color = "#",
		minVal,
		maxVal,
		nVal,
		val,
		i;
	for(i=1; i <= 6; i+=2){
		minVal = Number(h2d(minColor.substr(i,2)));
		maxVal = Number(h2d(maxColor.substr(i,2)));
		nVal = minVal + (maxVal-minVal) * (depth/maxDepth);
		val = d2h(Math.floor(nVal));
		while(val.length < 2){
			val = "0"+val;
		}
		color += val;
	}
	return color;
};

IG$._pcolor = function(c) {
	function h2d(h) {return parseInt(h,16);}
	
	var i, r = [];
	
	for (i=1; i<=6; i+=2) {
		r.push(Number(h2d(c.substr(i,2))));
	}
	
	return r;
}

IG$._interpolate_color = function(minColor,maxColor,ratio){
	var color = [],
		minVal,
		maxVal,
		nVal,
		val,
		i;
	for(i=0; i < 3; i++)
	{
		minVal = minColor[i];
		maxVal = maxColor[i];
		nVal = minVal + (maxVal-minVal) * ratio;
		val = Math.floor(nVal);
		color.push(val);
	}
	return color;
};

IG$.placeHolder = function(ctrl) {
	var browser = window.bowser,
		placeholder = ctrl.attr("placeholder");
	
	if (browser.msie && placeholder)
	{
		ctrl.bind({
			"focus": function(e) {
				var input = $(this);
				if (input.val() == input.attr("placeholder")) {
					input.val("");
					input.removeClass("placeholder");
				}
			},
			"blur": function(e) {
				var input = $(this);
				if (input.val() == "" || input.val() == input.attr("placeholder")) {
					input.addClass("placeholder");
					input.val(input.attr("placeholder"));
				}
				else
				{
					input.removeClass("placeholder");
				}
			},
			"keyup": function(e) {
				var input = $(this);
				if (input.val() == "") {
					input.blur();
				}
			}
		}).blur();
	}
}

IG$.CopyObject = function(src, tgt, attr) {
	var i,key;
	
	attr = (attr) ? ";" + attr + ";" : attr;
	
	tgt = (!tgt) ? {} : tgt;
	
	for (key in src)
	{
		if (attr && attr.indexOf(";" + key+";") > -1)
		{
			tgt[key] = src[key];
		}
		else if (!attr)
		{
			tgt[key] = src[key];
		}
	}
	return tgt;
}

