IG$.parseValueList = function(jdoc) {
	var i, j, clen, cfield, m, sfield,
		item,
		obj, dnode, dnodes, uid, vnode, delimiter, d,
		snode,
		hnode,
		hnodes,
		dnode,
		tpnode, tpnodes,
		vnode,
		cols, cols_m1,
		mval, dt, vt,
		results = [];
	
	if (jdoc)
	{
		for (m=0; m < jdoc.length; m++)
		{
			snode = jdoc[m];
			hnodes = snode.headers;
			dnode = snode.inline_data;
			
			obj = snode;
			obj.data = [];
			
			if (snode && hnodes && dnode)
			{
				delimiter = snode.delimiter;
				cfield = snode.codefield;
				sfield = snode.sortfield;
				cols = snode.cols;
				cols_m1 = cols-1;
				
				clen = hnodes.length;
				
				for (i=0; i < dnode.length; i++)
				{
					mval = dnode[i]; // .split(delimiter);
					obj.data.push({
						code: mval[0],
						disp: mval[1],
						sdisp: mval[2]
					});
				}
			}
			
			if (obj)
			{
				results.push(obj);
			}
		}
	}
	
	return results;
}


