/**
 * javascript utility to format number value with comma separator
 * @memberof module:common/mainapp
 */
IG$.FormatNumber = function(value)
{
	value += "";
	x = value.split(".");
	x1 = x[0];
	x2 = x.length > 1 ? "." + x[1] : "";
	var rgx = /(\d+)(\d{3})/;
	while (rgx.test(x1)) {
		x1 = x1.replace(rgx, "$1" + "," + "$2");
	}
	return x1 + x2;
}

/**
 * javascript utility fucntion to number format
 * @memberof module:common/mainapp
 * @param format {string} formatstring
 * @param value {number} number value
 */
IG$.NumberFormat = function(format, value) {
	// ###.####
	var p = 0,
		postfix = "",
		postfix_trim = "",
		prefix = "",
		sformat = "",
		dformat = "",
		baseunit = 0, bunitformat,
		pval, n, np, i,
		c, pc, fval, b_decimal, b_format, e_format, b_else, sbase, lbase, grouping, last_decimal, integer_part, decimal_part;
	
	if (format)
	{
		var dv = 0,
			sformat;
		
		if (format.indexOf(";") > -1)
		{
			sformat = format.substring(format.indexOf(";") + 1);
			format = format.substring(0, format.indexOf(";"));
		}
		
		for (i=format.length-1; i>=0; i--)
		{
			c = format.charAt(i);
			if (c == '#' || c == '0')
				break;
			if (c == ',')
				dv++;
		}
		
		if (dv > 0)
		{
			value = value / (Math.pow(1000, dv));
		}
		else if (sformat)
		{
			if (sformat.substring(0, 5) == "UNIT,")
			{
				sformat = sformat.substring("UNIT,".length);
				sformat = sformat.split(",");
				
				var vformats = [],
					vf;
				
				for (i=0; i < sformat.length; i++)
				{
					var s = sformat[i],
						s1, s2;
						
					if (s.indexOf("=") > -1)
					{
						s1 = s.substring(0, s.indexOf("="));
						s2 = s.substring(s.indexOf("=") + 1);
						
						s2 = Number(s2);
						
						if (s1 && !isNaN(s2))
						{
							vformats.push({s1: s1, s2: s2});
						}
					}
				}
				
				if (vformats.length)
				{
					for (i=vformats.length-1; i>=0; i--)
					{
						if (value >= vformats[i].s2)
						{
							vf = vformats[i];
							break;
						}
					}
					
					if (vf)
					{
						value = value / vf.s2;
						format += vf.s1;
					}
				}
			}
		}
		
		for (i=0; i < format.length; i++)
		{
			c = format.charAt(i);
			b_else = 0;
			if (b_format && c == ".")
			{
				b_decimal = 1;
				last_decimal = pc;
			}
			else if (c == "#" || c == "0" || c == "-")
			{
				if (b_format && b_decimal && c != "-")
				{
					p++;
				}
				
				b_format = 1;
			}
			else if (c == ",")
			{
				if (b_format)
				{
					if (pc == c)
					{
						baseunit++;
						lbase = i;
						if (!sbase)
						{
							sbase = i - 1;
						}
					}
					else
					{
						grouping = c;
					}
				}
				else
				{
					b_else = 1;
				}
			}
			else
			{
				if (b_format && !b_decimal && i > 0 && i+1 < format.length && (format.charAt(i+1) == "#" || format.charAt(i+1) == "0"))
				{
					grouping = c;
				}
				else if (grouping && c == grouping && b_format)
				{
					// do nothing
				}
				else
				{
					b_else = 1;
					
					if (b_format && !e_format)
						e_format = 1;
				}
			}
			
			if (b_else)
			{
				if (!b_format && !e_format)
				{
					prefix += c;
				}
				else
				{
					if (c == "%")
					{
						if (pc == "'" && postfix.length && postfix.charAt(postfix.length - 1) == "'")
						{
							postfix = postfix.substring(0, postfix.length - 1);
						}
						else
						{
							value = value * 100;
						}
					}
					
					postfix += c;
					
					if (c != " ")
					{
						postfix_trim += c;
					}
				}
			}
			
			pc = c;
		}
		
		n = postfix.indexOf("&permil;");
		if (n > -1)
		{
			if (n > 0 && postfix.charAt(n-1) == "'")
			{
				postfix = postfix.substring(0, n-1) + postfix.substring(n);
				n--;
			}
			else
			{
				value = value * 1000;
			}
			
			postfix = postfix.substring(0, n) + "‰" + postfix.substring(n + "&permil;".length);
		}
		
		if (baseunit > 0)
		{
			format = format.substring(0, sbase) + format.substring(lbase + 1);
			value = value / Math.pow(1000, baseunit);
		}
		
		sformat = format.substring(prefix.length, format.length - prefix.length - postfix.length);
	}
	
	fval = parseFloat(Math.round(value + "e" + p) + "e-" + p).toFixed(p);
	
	if (sformat)
	{
		n = fval.indexOf(".");
		
		integer_part = fval;
		
		if (n > -1)
		{
			integer_part = fval.substring(0, n);
			decimal_part = fval.substring(n+1);
		}
		
		if (grouping)
		{
			n = integer_part.length - 3;
			
			for (i=n; i>=0; i-=3)
			{
				if (i > 0)
				{
					integer_part = integer_part.substring(0, i) + grouping + integer_part.substring(i);
				}
			}
			
			if (last_decimal == "0" && n == 0)
			{
				integer_part = "0";
			}
		}
		
		np = sformat.indexOf(".");
		
		if (np > 0)
		{
			dformat = sformat.substring(np+1);
		}
		
		if (dformat)
		{
			var dvalue = [];
			
			for (i=0; i < dformat.length ;i++)
			{
				if (dformat.charAt(i) == "0")
				{
					dvalue.push(decimal_part.length > i ? decimal_part.charAt(i) : "0");
				}
				else if (dformat.charAt(i) == "#")
				{
					dvalue.push(decimal_part.length > i && decimal_part.charAt(i) != "0" ? decimal_part.charAt(i) : "");
				}
			}
			
			decimal_part = dvalue.join("");
		}
		
		fval = integer_part + (decimal_part ? "." + decimal_part : "");
	}
	
	return prefix + fval + postfix;
}

