/**
 * This function removes non-numeric characters
 * @memberof module:common/mainapp
 */
IG$.stripNonNumeric = function(str) {
  str += "";
  var rgx = /^\d|\.|-$/;
  var out = "";
  for( var i = 0; i < str.length; i++ )
  {
	if( rgx.test( str.charAt(i) ) ){
	  if( !( ( str.charAt(i) == "." && out.indexOf( "." ) != -1 ) ||
			 ( str.charAt(i) == "-" && out.length != 0 ) ) ){
		out += str.charAt(i);
	  }
	}
  }
  return out;
};

IG$.rsaPublicKeyModules = null;
IG$.rsaPublicKeyExpoenent = null;
IG$.rsa_kinfo = null;

IG$.encryptKey = function(str) {
	var i;
	
	if (IG$.rsaPublicKeyModules) 
	{
		var rsa = new RSAKey();
		rsa.setPublic(IG$.rsaPublicKeyModules, IG$.rsaPublicKeyExpoenent);
		
		for (i=0; i < str.length; i++)
		{
			str[i] = rsa.encrypt(str[i]);
		}
	}
	
	return str;
}

/**
 * remote procedure call common utility
 * @memberof module:common/mainapp
 */
IG$._rpc$ = function() {
	this.stoploading = true;
}

IG$._rpc$.prototype = {
	/**
	 * initialize rpc call
     * @memberof module:common/mainapp
     * @param panel {object} showing progress on panel
     * @param params {object} parameter to post request
     * @param caller {object} scope of rpc
     * @param rsSuccess {callback} callback object to success call
     * @param rsFaile {callback} callback object to failed call
     * @param rsParams {object} parameter for additional setup
	 */
	init: function(panel, params, caller, rsSuccess, rsFail, rsParams) {
		var me = this;
		me.panel = panel;
		me.params = params;
	
		me.caller = caller;
		me.rsSuccess = rsSuccess;
		me.rsFail = rsFail;
		me.rsParams = rsParams;

		me.showerror = true;
		me.stoploading = true;
		
		me.sccall = new IG$.callBackObj(me.caller, me.rsSuccess, me.rsParams);
		me.scfail = new IG$.callBackObj(me.caller, me.rsFail, me.rsParams);
	},
	
	_sproc: function(response, status, xhr) {
		var req = this,
			ret,
			p, k, pnames;
		
		window.Pace && window.Pace.stop();
				
		response = response || {
			errorcode: "0xffff",
			errormsg: 'Server incorrect responding'
		};
		
		var errcode = response.errorcode,
			stopprog = false;
		
		IG$.checkTimeout(response.session_timeout);
		
		if (errcode != "0x1300" && window.use_session_key && !ig$._session_key && response.session_key)
		{
			ig$._session_key = response.session_key;
			IG$.psst_set(1, response.session_key);
		}
		
		if (req.panel && req.panel.setLoading)
		{
			if (req.stoploading != false)
			{
				req.panel.setLoading(false);
			}
			else
			{
				stopprog = true;
			}
		}
		if (errcode == "0x1300")
		{
			IG$.psst_set(0);
			if (stopprog == true)
			{
				req.panel.setLoading(false);
			}
			ret = req.scfail.execute(errcode);
				
			if (ret == true || req.showerror == false)
				return;

			IG$.showLogin((req.panel ? new IG$.callBackObj(req.panel, req.panel.entryLogin) : null), 2);
		}
		else if (errcode != null && errcode.length > 0)
		{
			if (stopprog == true)
			{
				req.panel.setLoading(false);
			}
			
			var rerr = req.scfail.execute(response);
			
			if (req.showerror !== false && rerr != false)
			{
				IG$.ShowErrorMessage(response, req.panel, req.params);
			}
		}
		else
		{
			// IG$.ShowErrorMessage(jdoc, req.panel, req.params);
			req.sccall.execute(response.result);
		}	
	},
	
	/**
	 * sending request to server
     * @memberof module:common/mainapp
	 */
	send: function() {
		var req = this,
			skey,
			gparams;
		
		req.params._mts_ = IG$._g$a || req.params.mts || "";
		req.params._share_uid_ = IG$._share_uid_;
		if (window.use_session_key)
		{
			skey = $.cookie("amp_auth");
			req.params.session_key = ig$._session_key || skey || "empty";
		}
		req.params.uniquekey = IG$.getUniqueKey();
		
		window.Pace && window.Pace.start();
		
		if (req.panel && req.panel.setLoading)
		{
			req.panel.setLoading(true);
		}
		
		if (req.params.get_params && req._rmethod == "GET")
		{
			gparams = req.params.get_params;
		}
		
		$.ajax({
			url: req.servlet$ || ig$.servlet,
			data: req._rmethod && req._rparam !== true ? (gparams || "") : JSON.stringify(req.params),
			dataType: "json", // "text",
			type: req._rmethod || "POST",
			async: true,
			contentType: "application/json; charset=UTF-8", // "application/x-www-form-urlencoded; charset=UTF-8",
			timeout: 0, // 600000,
			beforeSend: function(xhr, settings) {
			},
			cache: false,
			crossDomain: false,
			processData: true,
			success: function(response, status, xhr) {
				req._sproc(response, status, xhr);
			},
			error: function(xhr, status, err) {
				window.Pace && window.Pace.stop();
				
				if (req.panel && req.panel.setLoading)
				{
					req.panel.setLoading(false);
				}
				
				if (req.showerror !== false)
				{
					IG$.ShowConnectionError(req.panel);
				}
				var response = {
					errorcode: '0x9999',
					errormsg: 'Server URL Connection Failed'
				};
				req.scfail.execute(response);
			}
		});
	}
};

/**
 * download file from server
 * @memberof module:common/mainapp
 * @param url {string} url to download
 * @param datas {object} request parameter
 * @param method {string} http request method
 */
$.download = function(url, datas, method){
	//url and data options required
	if( url && datas ){ 
		//data can be string of parameters or array/object
		//datas = typeof datas == "string" ? datas : jQuery.param(datas);
		//split params into form inputs
		var inputs = [],
			i;
		for (i=0; i < datas.length; i++)
		{
			inputs.push("<input type='hidden' name='"+ datas[i].name +"' value='"+ datas[i].value +"' />"); 
		}
		if (window.use_session_key)
		{
			inputs.push("<input type='hidden' name='session_key' value='"+ ig$._session_key +"' />");
		}
		//send request
		$("<form action='"+ url + "' method='"+ (method||"post") + "'>" + inputs.join("") + "</form>")
		.appendTo("body").submit().remove();
	};
};

