// date related functions

IG$.dateUtil = {
	datePartsRe: /^(?:(\d{1,4})|(\w{3,}))[/\-.\\\s](?:(\d{1,2})|(\w{3,}))[/\-.\\\s](\d{1,4})$/,
	// datePartsRe: /^(?:(\d{1,4})|(\w{3,}))[/\-.\\\s](?:(\d{1,2})|(\w{3,}))[/\-.\\\s](?:(\d{1,4})|(\w{3,}))$/,
	monthCodes: [
	  	{
	  		JAN: 0,
	  		FEB: 1,
	  		MAR: 2,
	  		APR: 3,
	  		MAY: 4,
	  		JUN: 5,
	  		JUL: 6,
	  		AUG: 7,
	  		SEP: 8,
	  		OCT: 9,
	  		NOV: 10,
	  		DEC: 11
	  	},
	  	{
	  		JANUARY: 0,
	  		FEBRUARY: 1,
	  		MARCH: 2,
	  		APRIL: 3,
	  		MAY: 4,
	  		JUNE: 5,
	  		JULY: 6,
	  		AUGUST: 7,
	  		SEPTEMBER: 8,
	  		OCTOBER: 9,
	  		NOVEMBER: 10,
	  		DECEMBER: 11,
		}
	],
	getMonthCode: function(value) {
		var i,
			mm = value.toUpperCase(),
			monthCodes = IG$.dateUtil.monthCodes;
		for (i=0; i < monthCodes.length; i++)
		{
			if (typeof monthCodes[i][mm] != "undefined")
			{
				return monthCodes[i][mm];
			}
		}
		
		return -1;
	},
	
	guessDate: function(value) {
		var format = [],
			placeholder = [],
			parts = IG$.dateUtil.datePartsRe.exec(value),
			day, month, year, dt, sep;

		// Regex couldn't parse; invalid date.
		if (!parts) 
		{
			return;
		}
		
		sep = parts[0].indexOf("-") > 0 ? "-" : "/";

		// Now the parts array will be:
		// [0] - the full string
		// [1] - The first token if numeric
		// [2] - The first token if alphabetic
		// [3] - The second token if numeric
		// [4] - The second token if alphabetic
		// [5] - The third token.
		// If they've used all numeric parts, we have to use locale order
		// to decide what the parts are. We have three valid choices:
		// d/m/y
		// m/d/y
		// y/m/d
		if (!parts[2] && !parts[4]) 
		{
			// Not in a y/m/d locale and (first character is a day token, or first
			// token is definitely a day) - it's d/m/y
			// eslint-disable-next-line no-undef
			if (parts[1] > 12 && parts[1] < 32 && parts[3] < 13)
			{
				day = parseInt(parts[1]);
				month = parseInt(parts[3]) - 1;
				year = parseInt(parts[5]);
				
				format = ["dd", "mm", "yy"];
				placeholder = ["dd", "mm", "yyyy"];
			}
			else if (parts[3] > 12 && parts[3] < 32 && parts[1] < 13) 
			{
				// Not in a y/m/d locale and (first charecter is a month token, or
				// first token is definitely a month) - it's m/d/y
				month = parseInt(parts[1]) - 1;
				day = parseInt(parts[3]);
				year = parseInt(parts[5]);
				
				format = ["mm", "dd", "yy"];
				placeholder = ["mm", "dd", "yyyy"];
			}
			else 
			{
				// y/m/d is the only other valid format
				year = parseInt(parts[1]);
				month = parseInt(parts[3]) - 1;
				day = parseInt(parts[5]);
				
				format = ["yy", "mm", "dd"];
				placeholder = ["yyyy", "mm", "dd"];
			}
		}
		else 
		{
			if (parts[2] && parts[4]) 
			{
				return null;
			}

			// m/d/y
			if (parts[2]) 
			{
				if (parts[5].length < 4)
					return;
				
				month = IG$.dateUtil.getMonthCode(parts[2]);
				day = parseInt(parts[3]);
				year = parseInt(parts[5]);
				
				format = ["M", "dd"];
				placeholder = ["mon", "dd"];
			}
			else 
			{ // d/m/y
				if (parts[5].length < 4)
					return;
				day = parseInt(parts[1]);
				month = IG$.dateUtil.getMonthCode(parts[4]);
				year = parseInt(parts[5]);
				
				format = ["dd", "M"];
				placeholder = ["dd", "mon"];
			}
			
			if (parts[5].length == 2)
			{
				// Short years must be upgraded according to the y2kYear setting
				if (year < 50)
				{
					year += 2000;
				}
				else if (year < 100) 
				{
					year += 1900;
				}
				
				format.push("y");
				placeholder.push("yy");
			}
			else if (parts[5].length == 4)
			{
				format.push("yy");
				placeholder.push("yyyy");
			}
			else
			{
				return;
			}
		}
		
		if (isNaN(year) || isNaN(month) || isNaN(day))
			return;
		
		// Alphabetic month couldn't be found, or numeric one out of range.
		if (month < 0 || month > 11 || day < 1 || day > 31)
		{
			return;
		}

		// Create the first of the month so that we can check the validity of the day
		dt = new Date(year, month, day, 0, 0, 0);
		
		return {
			format: format.join(sep),
			placeholder: placeholder.join(sep),
			date: dt
		};
	},
	
	createDatePicker: function(me, d, onselect) {
		IG$.dateUtil.updateDatePicker(d);
		
		d.datepicker({
			monthNames: $.datepicker._defaults.monthNamesShort,
			buttonImageOnly: true,
			constrainInput: false,
			changeMonth: true,
			changeYear: true,
			dateFormat: IG$.calendar_locale.c1format,
			defaultDate: 0,
			onSelect: function(dateText, inst) {
				if (onselect)
				{
					onselect.apply(me, [dateText, inst]);
				}
			}
		});
		
		return d;
	},
	
	updateDatePicker: function(dpicker) {
		var tinput = dpicker;
		
		// tinput.on("keyup blur change", function(e) {
		tinput.on("keydown keyup", function(e) {
			var me = $(this),
				tvalue = me.val(),
				tdate,
				kcode;
			
			kcode = e.keyCode;
			
			// if (kcode == 8 || kcode == 16 || kcode == 36 || kcode == 37 || kcode == 39)
			if (kcode != 13)
			{
				return;
			}
			
			e.preventDefault();
			e.stopPropagation();
			e.stopImmediatePropagation();
			
			if (e.type == "keyup")
			{
				tdate = IG$.dateUtil.guessDate(tvalue);
				
				if (tdate)
				{
					me.attr("placeholder", tdate.placeholder);
					me.datepicker("option", "dateFormat", tdate.format);
					me.datepicker("setDate", tdate.date);
					var dpicker = $.datepicker._getInst(me[0]);
					dpicker.settings.onSelect.apply(me, [tvalue, dpicker]);
				}
			}
			
			return false;
		});
		
		tinput.on("click", function(e) {
			var me = $(this);
			me.datepicker("show");
		})
	}
};

IG$.guid = function() {
	var lut,
		i;
	
	if (!IG$._guid_lut)
	{
		lut = IG$._guid_lut = [];

		for (i=0; i<256; i++) { 
			lut[i] = (i<16?'0':'')+(i).toString(16); 
		}
	}
	else
	{
		lut = IG$._guid_lut;
	}
	
	var d0 = Math.random()*0xffffffff|0,
		d1 = Math.random()*0xffffffff|0,
		d2 = Math.random()*0xffffffff|0,
		d3 = Math.random()*0xffffffff|0,
		guid;
	
	guid = lut[d0&0xff]+lut[d0>>8&0xff]+lut[d0>>16&0xff]+lut[d0>>24&0xff]+'-'+
	lut[d1&0xff]+lut[d1>>8&0xff]+'-'+lut[d1>>16&0x0f|0x40]+lut[d1>>24&0xff]+'-'+
	lut[d2&0x3f|0x80]+lut[d2>>8&0xff]+'-'+lut[d2>>16&0xff]+lut[d2>>24&0xff]+
	lut[d3&0xff]+lut[d3>>8&0xff]+lut[d3>>16&0xff]+lut[d3>>24&0xff];
	
	return guid;
}

IG$._inpEdt$ = function(txtarea, value) {
	var el = $("textarea", txtarea.body.dom),
		v = txtarea.getRawValue(),
		s2 = -1,
		rvalue;
	
	rvalue = "[" + value + "]";
	
	el.focus();
		
	if (v.length > 0)
	{
		var sm = IG$._inpEd$pos(el[0]),
			sm_start = sm != -1 && sm ? sm.start : -1;
		// replace selection
		
		if (sm_start < 0)
		{
			sm_start = v.length;
		}
		v = v.substring(0, sm_start) + rvalue + v.substring(sm_start);
		s2 = sm_start + rvalue.length;
	}
	else
	{
		v = rvalue;
		s2 = v.length;
	}
	
	txtarea.setValue(v);
	
	if (s2)
	{
		el.prop("selectionEnd", s2);
		
		setTimeout(function() {
			txtarea.focus();
			IG$._inpEd$sep(el[0], s2, s2);
		}, 100);
	}
}

IG$._inpEd$pos = function getCursorPos(input) {
	if ("selectionStart" in input && document.activeElement == input) 
	{
		return {
			start: input.selectionStart,
			end: input.selectionEnd
		};
	}
	else if (input.createTextRange) 
	{
		var sel = document.selection.createRange();
		if (sel.parentElement() === input) 
		{
			var rng = input.createTextRange();
			rng.moveToBookmark(sel.getBookmark());
			for (var len = 0; rng.compareEndPoints("EndToStart", rng) > 0; rng.moveEnd("character", -1)) 
			{
				len++;
			}
			rng.setEndPoint("StartToStart", input.createTextRange());
			for (var pos = { start: 0, end: len }; rng.compareEndPoints("EndToStart", rng) > 0; rng.moveEnd("character", -1)) 
			{
				pos.start++;
				pos.end++;
			}
			return pos;
		}
	}
	return -1;
};

IG$._inpEd$sep = function(input, selectionStart, selectionEnd) {
	if (input.setSelectionRange) 
	{
		input.focus();
		input.setSelectionRange(selectionStart, selectionEnd);
	}
	else if (input.createTextRange) 
	{
		var range = input.createTextRange();
		range.collapse(true);
		range.moveEnd('character', selectionEnd);
		range.moveStart('character', selectionStart);
		range.select();
	}
}


IG$._ws = function(opt) {
	this._init(opt);
};

IG$._ws.prototype = {
	_init: function(opt) {
		opt.protocol = opt.protocol || "ws";
		var me = this,
			url = opt.url,
			ws_url = opt.protocol + "://" + url + "/websocket/endpoint",
			ws;
		
		me._userdetail = opt.userdetail;
		
		try
		{
			ws = me.__ws = new WebSocket(ws_url);
			ws.onopen = function (e) {
				me.onopen(e);
			};
			
			ws.onmessage = function (e) {
				me.onmessage(e);
			};
			
			ws.onclosed = function(e) {
				me.onclosed(e);
			};
			
			ws.onclosing = function(e) {
				me.onclosing(e);
			}
		}
		catch (e)
		{
		}
	},
	onopen: function(e) {
		var me = this,
			userdetail = me._userdetail;
		me.send("_main_", "login", {email: userdetail.email, name: userdetail.name, id: userdetail.id});
	},
	onmessage: function(e) {
		var m = JSON.parse(e.data),
			msg;
		
		if (m.application == "_main_")
		{
			switch (m.action)
			{
			case "login":
				msg = "Logged in user <ul><li>" + m.content.name + " (" + m.content.id + ")</li><li>" + m.content.email + "</li></ul>";
				break;
			case "writecontent":
				msg = "Content updated on server! <ul><li>" + m.content.nodepath + "</li></li>By: " + m.content.username + "</li></ul>";
				break;
			}
		}
		else if (m.parent_header && m.parent_header.csession)
		{
			// python results
			var vsession,
				_ipt_location = IG$._ipt_location,
				ipt;
			
			if (_ipt_location && _ipt_location[m.parent_header.csession])
			{
				ipt = _ipt_location[m.parent_header.csession];
				ipt.applyPython.call(ipt, m);
			}
		}
		
		if (msg)
		{
			IG$.alertmsg(ig$.appname, msg, null, null, 0, "info");
		}
	},
	send: function(application, action, data) {
		var me = this,
			ws = me.__ws,
			userdetail = me._userdetail,
			t = {
				application: application,
				action: action,
				sid: userdetail.sid,
				gid: userdetail.gid,
				mts: userdetail.mts,
				token: userdetail.token,
				id: IG$.guid(),
				content: data
			};
		
		ws.send(JSON.stringify(t));
	}
}

IG$._menu_button = function(btn, menus, options) {
	var me = this,
		doc = $(document),
		t, i;
	
	me._menus = menus;
	
	IG$._menu_btns = IG$._menu_btns || [];
	
	IG$._menu_btns.push(me);
	
	t = "<ul class='dropdown-menu animated";
	
	if (options.btn_styles)
	{
		for (i=0; i < options.btn_styles.length; i++)
		{
			t += " " + options.btn_styles[i];
		}
	}
	
	t += " m-t-xs'></ul>";
	
	me._b_html = $(t).appendTo("body");
	
	btn.bind("click", function(e) {
		e.stopPropagation();
		e.preventDefault();
		
		$.each(IG$._menu_btns, function(i, m) {
			if (m != me)
			{
				m._g_unbind();
			}
		});
		
		me._b_html.css(options.menu_position);
		me._b_html.toggle();
		IG$._menu_btn = me;
		doc.bind("click", me._g_unbind);
	});
}

IG$._gitemname = function(itemname) {
	var r = "";
	if (itemname)
	{
		r = "<span class='igc-itemname' title='" + itemname + "'> (" + itemname + ")</span>";
	}
	return r;
}

IG$._menu_button.prototype = {
	_g_unbind: function(e) {
		var doc = $(document),
			_menu_btns = IG$._menu_btns,
			me = IG$._menu_btn;
		
		if (me)
		{
			me._b_html.hide();
			doc.unbind("click", me._g_unbind);
		}
		IG$._menu_btn = null;
	},
	
	create: function() {
		var me = this;
		
		me._b_html.empty();
		
		$.each(me._menus, function(i, menu) {
			var t = "<li><a class='btn_button'>" + menu.text + "</a></li>",
				menu_btn = $(t).appendTo(me._b_html);
			
			if (menu.style)
			{
				$.each(menu.style, function(j, sname) {
					menu_btn.addClass(sname);
				});
			}
			
			menu_btn.bind("click", function(e) {
				e.preventDefault();
				e.stopPropagation();
				
				if (menu.handler)
				{
					menu.handler.apply(null);
				}
				
				me._b_html.hide();
				me._g_unbind();
			});
		});
	},
	
	setMenus: function(menus) {
		var me = this;
		me._menus = menus;
		
		me.create();
	}
}

IG$.__chartoption.chartext.$export_echart = function() {
	var svgnode,
		nodename,
		svg,
		chart = this.customchart,
		pobj = {};
		
	if (!chart)
		return;
	
	svgnode = IG$.getFstChld(chart.getDom());
	if (IG$.XGetNodeName(svgnode).toUpperCase() == "DIV")
	{
		svgnode = IG$.getFstChld(svgnode);
		nodename = IG$.XGetNodeName(svgnode).toUpperCase();
		
		if (nodename == "SVG")
		{
			IG$.XSetAttr(svgnode, "xmlns:xlink", "http://www.w3.org/1999/xlink");
			IG$.XSetAttr(svgnode, "xmlns", "http://www.w3.org/2000/svg");
			IG$.XSetAttr(svgnode, "version", "1.1");

			svg = IG$.toXMLString(svgnode);
			pobj.image_type = "svg";
			pobj.image_data = Base64.encode(svg); 
		}
		else
		{
			svg = chart.getConnectedDataURL({
				type: "png",
				pixelRatio: 2,
				backgroundColor: '#fff'
			});
			pobj.image_type = "png";
			pobj.image_data = svg;
		}
	}
	
	return pobj;
}